<template>
  <div class="real-time-statistics view-list">
    <div class="content">
      <div class="header-sub">
        <div class="title">
          <div class="breadcrumb-item-link">实时数据分析</div>
        </div>
      </div>
      <div class="container-box">
        <div class="left">
          <div class="left-title">
            <Icon
              class="icon"
              type="md-search"
              style="z-index: 99"
              @click="loadTableData"
            />
            <Input
              class="serch-input"
              v-model="search.name"
              :maxlength="50"
              placeholder="请输入设备名称/编码"
              @on-enter="loadTableData"
            >
            </Input>
          </div>
          <div class="left-box">
            <li
              v-for="(item, index) in tableData"
              @click="clickLi(item, index)"
              :class="clicked === index ? 'active' : ''"
            >
              <div class="li-title">
                <div
                  class="li-left"
                  :title="item.deviceName.length > 10 ? item.deviceName : ''"
                >
                  {{ item.deviceName }}
                </div>
                <div
                  class="li-right"
                  :class="item.deviceStatus == 1 ? 'green' : 'red'"
                >
                  {{ item.deviceStatusName }}
                </div>
              </div>
              <div class="li-content">
                <div class="li-code">
                  <span> {{ $t("common.text.device_code") }}：</span
                  ><span
                    class="title"
                    :title="item.deviceCode.length > 15 ? item.deviceCode : ''"
                    >{{ item.deviceCode }}</span
                  >
                </div>
                <div class="li-code">
                  <span>{{ $t("common.text.device_model") }}：</span
                  ><span
                    class="title"
                    :title="item.modelName.length > 15 ? item.modelName : ''"
                    >{{ item.modelName }}</span
                  >
                </div>
              </div>
            </li>
          </div>
        </div>
        <div class="right">
          <div class="right-title">选择数据项:</div>
          <div class="control-menu">
            <Button style="width:100px" @click="showDrawer">控制</Button>
            <div class="common-flex">
              <div>自动刷新：</div>
              <i-switch
                v-model="autoRefresh"
                @on-change="changeAutoRefresh"
                size="large"
              >
                <span slot="open">开启</span>
                <span slot="close">关闭</span>
              </i-switch>
            </div>
          </div>
          <div class="right-boxes">
            <div class="box" v-for="(item, index) in pointList" :key="index">
              <div class="nameline">
                {{ item.itemName }} - {{ item.itemCode }}
              </div>
              <div class="value">{{ item.value }}{{ item.unit }}</div>
            </div>
          </div>
          <div class="user-table">
            <Table
              class="m-table no-scroll no-border"
              stripe
              height="200"
              :loading="pageInfo.loading"
              :columns="tableColumns"
              :row-class-name="rowClassName"
              @on-row-click="onRowClick"
              :data="orderData"
            ></Table>
          </div>
        </div>
      </div>
    </div>

    <custom-drawer
      ref="drawer"
      :title="drawerTitle"
      :formValidate="formValidate"
      :formList="formList"
      v-model="formValue"
      @confirm="confirm"
    ></custom-drawer>
  </div>
</template>

<script>
import ChartBase from '@/components/chart-base'
import customDrawer from '@/components/common-drawer/common-drawer'
import {
  getDeviceItemData,
  getDeviceList,
  getRealtimePoint,
  getOrderList
} from '@/api/device'
import {
  getQiTingfenkong,
  getGonglvModefenkong,
  getSystemOutput,
  getIfClear,
  getSystemStatus,
  getSystemAutofenpei,
  getBingLi,
  getWaiting,
  getYouGong,
  getWuGong,
  postOrder
} from '@/api/control'
import moment from 'moment'

export default {
  name: 'real-time-statistics',
  components: { ChartBase, customDrawer },
  data () {
    return {
      orderData: [],
      maxlength: 50,
      selectLocation: '',
      tableColumns: [
        {
          title: '序号',
          key: 'id',
          align: 'left'
        },
        {
          title: '点位名称',
          key: 'orderContent',
          align: 'left',
          render: (h, params) => {
            if (params.row.orderContent) {
              let info = JSON.parse(params.row.orderContent)
              return h('div', info.pointName)
            }
          }
        },
        {
          title: '执行参数',
          key: 'orderContent',
          align: 'left',
          render: (h, params) => {
            if (params.row.orderContent) {
              let info = JSON.parse(params.row.orderContent)
              return h('div', info.pointValue)
            }
          }
        },
        {
          title: '执行情况',
          key: 'executeStatus',
          align: 'left',
          render: (h, params) => {
            switch (params.row.executeStatus) {
              case 'FAIL':
                return h('div', '下发失败')
              case 'WAIT_DELIVERY':
                return h('div', '等待下发')
              case 'SUCCESS':
                return h('div', '下发成功')
            }
          }
        },
        {
          title: '生效时间',
          key: 'executeTime',
          align: 'left'
        }
      ],
      formList: [],
      formValue: {},
      formValidate: {},
      chartOption: {},
      pointList: [],
      search: {
        name: '',
        status: ''
      },
      // baseInfo: {},
      currentId: '',
      rangeTime: [
        moment()
          .subtract(1, 'days')
          .format('YYYY-MM-DD HH:mm:ss'),
        moment().format('YYYY-MM-DD HH:mm:ss')
      ],
      deviceId: '',
      tableData: [],
      deviceName: [],
      realData: [],
      clicked: 0,
      pageInfo: {
        loading: false,
        pageIndex: 1,
        pageSize: 10,
        totalElements: 0
      },
      autoRefresh: true,
      flushInterval: 10,
      timer: '',
      controlType: '',
      drawerTitle: '',
      hasValidate: 0,
      orderTimer: null
    }
  },
  watch: {
    // baseInfo() {
    //   this.loadRealData();
    //   this.loadLineData();
    // },
    deviceId (v) {
      this.itemIds = []
      this.chartOption = {}
      if (this.timer) {
        clearInterval(this.timer)
      }
      this.loadPointData(v)
      if (this.autoRefresh) {
        this.timer = setInterval(() => {
          this.loadPointData(v)
        }, this.flushInterval * 1000)
      }
      // this.loadLineData();
    }
  },
  destroyed () {
    if (this.timer) {
      clearInterval(this.timer)
    }
    if (this.orderTimer) {
      clearInterval(this.orderTimer)
    }
  },
  methods: {
    getQiTingfenkong () {
      return new Promise((resolve, reject) => {
        getQiTingfenkong({ deviceId: this.deviceId }).then(res => {
          if (res.code === 200) {
            let array = res.data.map(i => {
              return {
                name: i.name,
                value: i.key + ',' + i.value
              }
            })
            resolve(array)
          }
        })
      })
    },
    rowClassName (row, index) {
      if (row.id == this.currentIndex) {
        return 'demo-table-info-row'
      }
    },
    onRowClick () {},
    getGonglvModefenkong () {
      return new Promise((resolve, reject) => {
        getGonglvModefenkong({ deviceId: this.deviceId }).then(res => {
          if (res.code === 200) {
            let array = res.data.map(i => {
              return {
                name: i.name,
                value: i.key + ',' + i.value
              }
            })
            resolve(array)
          }
        })
      })
    },
    getSystemOutput () {
      return new Promise((resolve, reject) => {
        getSystemOutput({ deviceId: this.deviceId }).then(res => {
          if (res.code === 200) {
            resolve(res.data.key)
          }
        })
      })
    },
    getIfClear () {
      return new Promise((resolve, reject) => {
        getIfClear({ deviceId: this.deviceId }).then(res => {
          if (res.code === 200) {
            let array = res.data.map(i => {
              return {
                name: i.name,
                value: i.key + ',' + i.value
              }
            })
            resolve(array)
          }
        })
      })
    },
    getSystemStatus () {
      return new Promise((resolve, reject) => {
        getSystemStatus({ deviceId: this.deviceId }).then(res => {
          if (res.code === 200) {
            let array = res.data.map(i => {
              return {
                name: i.name,
                value: i.key + ',' + i.value
              }
            })
            resolve(array)
          }
        })
      })
    },
    getSystemAutofenpei () {
      return new Promise((resolve, reject) => {
        getSystemAutofenpei({ deviceId: this.deviceId }).then(res => {
          if (res.code === 200) {
            resolve(res.data.key)
          }
        })
      })
    },
    getBingLi () {
      return new Promise((resolve, reject) => {
        getBingLi({ deviceId: this.deviceId }).then(res => {
          if (res.code === 200) {
            let array = res.data.map(i => {
              return {
                name: i.name,
                value: i.key + ',' + i.value
              }
            })
            resolve(array)
          }
        })
      })
    },
    getWaiting () {
      return new Promise((resolve, reject) => {
        getWaiting({ deviceId: this.deviceId }).then(res => {
          if (res.code === 200) {
            let array = res.data.map(i => {
              return {
                name: i.name,
                value: i.key + ',' + i.value
              }
            })
            resolve(array)
          }
        })
      })
    },
    getYouGong () {
      return new Promise((resolve, reject) => {
        getYouGong({ deviceId: this.deviceId }).then(res => {
          if (res.code === 200) {
            resolve(res.data.key)
          }
        })
      })
    },
    getWuGong () {
      return new Promise((resolve, reject) => {
        getWuGong({ deviceId: this.deviceId }).then(res => {
          if (res.code === 200) {
            resolve(res.data.key)
          }
        })
      })
    },
    async showDrawer () {
      switch (this.controlType) {
        case 'SINGLE_FCU':
          this.drawerTitle = '单机FCU控制'
          if (!this.hasValidate) {
            let qiTing = await this.getQiTingfenkong()
            let gongLvMode = await this.getGonglvModefenkong()
            let sysKey = await this.getSystemOutput()
            let ifClear = await this.getIfClear()
            let systemStatus = await this.getSystemStatus()
            this.formValidate = {
              one: [{ required: true, message: '一键控制不能为空' }],
              two: [{ required: true, message: '功率控制模式不能为空' }],
              three: [{ required: true, message: '系统输出功率不能为空' }],
              four: [{ required: true, message: '故障清除不能为空' }],
              five: [{ required: true, message: '系统状态设定不能为空' }]
            }
            this.formList = [
              {
                label: '一键控制',
                type: 'select',
                value: 'one',
                data: qiTing
              },
              {
                label: '功率控制模式',
                type: 'select',
                value: 'two',
                data: gongLvMode
              },
              {
                label: '系统输出功率',
                type: 'input',
                value: 'three',
                placeholder: '请录入总功率（平局分配）',
                unit: 'W',
                key: sysKey,
                inputType: 'number'
              },
              {
                label: '故障清除',
                type: 'radio',
                value: 'four',
                data: ifClear
              },
              {
                label: '系统状态设定',
                type: 'select',
                value: 'five',
                data: systemStatus
              }
            ]
            this.hasValidate++
          }
          break
        case 'ALL_FCU':
          this.drawerTitle = '整机FCU控制'
          if (!this.hasValidate) {
            let qiTing = await this.getQiTingfenkong()
            let fenpeiKey = await this.getSystemAutofenpei()
            let ifClear = await this.getIfClear()

            this.formValidate = {
              one: [{ required: true, message: '整机控制不能为空' }],
              two: [{ required: true, message: '系统功率自动分配不能为空' }],
              three: [{ required: true, message: '故障清除不能为空' }]
            }
            this.formList = [
              {
                label: '整机控制',
                type: 'select',
                value: 'one',
                data: qiTing
              },
              {
                label: '系统状态设定',
                type: 'input',
                value: 'two',
                unit: 'KW',
                key: fenpeiKey,
                inputType: 'number'
              },
              {
                label: '故障清除',
                type: 'radio',
                value: 'three',
                data: ifClear
              }
            ]
            this.hasValidate++
          }
          break
        case 'PCS':
          this.drawerTitle = 'PCS控制'
          if (!this.hasValidate) {
            let qiTing = await this.getQiTingfenkong()
            let bingLi = await this.getBingLi()
            let ifClear = await this.getIfClear()
            let wait = await this.getWaiting()
            let youGong = await this.getYouGong()
            let wuGong = await this.getWuGong()
            this.formValidate = {
              one: [{ required: true, message: 'PCS开关机不能为空' }],
              two: [{ required: true, message: '并离网不能为空' }],
              three: [{ required: true, message: '故障清除不能为空' }],
              four: [{ required: true, message: 'PCS待机模式不能为空' }],
              five: [{ required: true, message: '有功功率不能为空' }],
              six: [{ required: true, message: '无功功率不能为空' }]
            }
            this.formList = [
              {
                label: '整机控制',
                type: 'select',
                value: 'one',
                data: qiTing
              },
              {
                label: '并离网',
                type: 'radio',
                value: 'two',
                data: bingLi
              },
              {
                label: '故障清除',
                type: 'radio',
                value: 'three',
                data: ifClear
              },
              {
                label: 'PCS待机模式',
                type: 'select',
                value: 'four',
                data: wait
              },
              {
                label: '有功功率设定',
                type: 'input',
                value: 'five',
                unit: 'KW',
                placeholder: '请录入有功功率',
                key: youGong,
                inputType: 'number'
              },
              {
                label: '无功功率设定',
                type: 'input',
                value: 'six',
                unit: 'KW',
                placeholder: '请录入无功功率',
                key: wuGong,
                inputType: 'number'
              }
            ]
            this.hasValidate++
          }
          break
        default:
          this.drawerTitle = '电池控制'
          break
      }
      this.$refs.drawer.resetFields()
      this.$refs.drawer.open()
    },
    confirm () {
      let values = this.formValue
      let orders = []
      switch (this.controlType) {
        case 'SINGLE_FCU':
          orders.push({
            pointName: values.one.split(',')[0],
            pointValue: values.one.split(',')[1]
          })
          orders.push({
            pointName: values.two.split(',')[0],
            pointValue: values.two.split(',')[1]
          })
          orders.push({
            pointName: this.formList[2].key,
            pointValue: values.three
          })
          orders.push({
            pointName: values.four.split(',')[0],
            pointValue: values.four.split(',')[1]
          })
          orders.push({
            pointName: values.five.split(',')[0],
            pointValue: values.five.split(',')[1]
          })
          break
        case 'ALL_FCU':
          orders.push({
            pointName: values.one.split(',')[0],
            pointValue: values.one.split(',')[1]
          })
          orders.push({
            pointName: this.formList[1].key,
            pointValue: values.two
          })
          orders.push({
            pointName: values.three.split(',')[0],
            pointValue: values.three.split(',')[1]
          })
          break
        case 'PCS':
          orders.push({
            pointName: values.one.split(',')[0],
            pointValue: values.one.split(',')[1]
          })
          orders.push({
            pointName: values.two.split(',')[0],
            pointValue: values.two.split(',')[1]
          })
          orders.push({
            pointName: values.three.split(',')[0],
            pointValue: values.three.split(',')[1]
          })
          orders.push({
            pointName: values.four.split(',')[0],
            pointValue: values.four.split(',')[1]
          })
          orders.push({
            pointName: this.formList[4].key,
            pointValue: Number(values.five) * 100
          })
          orders.push({
            pointName: this.formList[5].key,
            pointValue: Number(values.six) * 100
          })
          break
        default:
          break
      }
      postOrder({ orders }).then(res => {
        if (res.code === 200) {
          this.getOrderList(res.data)
          this.orderTimer = setInterval(() => {
            this.getOrderList(res.data)
          }, 5000)
          this.$Message.success('控制命令下发成功')
        } else {
          this.$Message.error('控制命令下发失败')
        }
      })
      this.$refs.drawer.close()
    },
    getOrderList (orderId) {
      getOrderList({orderId}).then(res => {
        if (res.code === 200) {
          this.orderData = res.data
        } else {
          this.$Message.error(res.msg)
        }
      })
    },
    changeAutoRefresh (e) {
      if (e) {
        this.timer = setInterval(() => {
          this.loadPointData(this.deviceId)
        }, this.flushInterval * 1000)
      } else {
        clearInterval(this.timer)
      }
    },
    clickLi (item, index) {
      if (this.timer) {
        clearInterval(this.timer)
      }
      if (this.orderTimer) {
        clearInterval(this.orderTimer)
      }
      this.clicked = index
      this.deviceId = item.id
      this.controlType = item.deviceControlMode
      this.hasValidate = 0
      this.formValidate = {}
      this.formList = []
      this.formValue = {}
      this.orderData = []
      // this.loadPointData(item.id)
    },
    checkDate (item) {
      item.checked = !item.checked
      if (item.checked) {
        this.itemIds.push(item.itemId)
        this.deviceName.push(item.alias)
      } else {
        this.itemIds.map((d, index) => {
          if (item.itemId == d) {
            this.itemIds.splice(index, 1)
            this.deviceName.splice(index, 1)
          }
        })
      }
    },
    checkItemId (item) {
      item.checked = !item.checked
      this.itemIds.push(item.itemId)
    },

    loadRealData () {
      getDeviceItemData({
        // deviceId: this.baseInfo.deviceId
        deviceId: this.deviceId
      }).then(res => {
        this.realData = (res.data.result || []).map(d => ({
          ...d,
          checked: false
        }))
      })
    },
    loadPointData (id) {
      getRealtimePoint({ id }).then(res => {
        if (res.code === 200) {
          this.pointList = res.data.items.map(i => {
            return {
              ...i
            }
          })
          this.flushInterval = res.data.flushInterval
        }
      })
    },
    rgb () {
      var r = Math.floor(Math.random() * 256)
      var g = Math.floor(Math.random() * 256)
      var b = Math.floor(Math.random() * 256)
      var rgb = '(' + r + ',' + g + ',' + b + ')'
      return rgb
    },
    color () {
      // 十六进制颜色随机
      var r = Math.floor(Math.random() * 256)
      var g = Math.floor(Math.random() * 256)
      var b = Math.floor(Math.random() * 256)
      var color = '#' + r.toString(16) + g.toString(16) + b.toString(16)
      return color
    },
    line: function (time, itemDatas, deviceName) {
      this.chartOption = {
        grid: {
          x: 80,
          y: 20,
          x2: 80,
          y2: 50,
          containLabel: true
        },
        // legend: {
        //   data:deviceName,
        //   x: 'center',
        //   y: 'top'
        // },
        tooltip: {
          trigger: 'axis'
        },
        color: [
          '#ff7f50',
          '#87cefa',
          '#da70d6',
          '#32cd32',
          '#6495ed',
          '#ff69b4',
          '#ba55d3',
          '#cd5c5c',
          '#ffa500',
          '#40e0d0',
          '#1e90ff',
          '#ff6347',
          '#7b68ee',
          '#00fa9a',
          '#ffd700',
          '#6699FF',
          '#ff6666',
          '#3cb371',
          '#b8860b',
          '#30e0e0'
        ],
        // dataZoom: [
        //   {
        //     show: true,
        //     realtime: true,
        //     backgroundColor:"rgba(47,69,84,0)",
        //     borderColor:"#ddd",
        //     fillerColor:"rgba(167,183,204,0.4)",
        //     zoomOnMouseWheel:true,
        //     moveOnMouseMove:true,
        //     bottom:"2%",
        //   }
        // ],
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: time,
          // x轴的颜色和宽度
          splitLine: {
            show: true,
            lineStyle: {
              color: ['#dedede']
            }
          },

          axisLine: {
            lineStyle: {
              color: '#d2d2d2',
              width: 1 // 这里是坐标轴的宽度,可以去掉
            }
          }
        },
        dataZoom: [
          {
            // type: "slider"
            type: 'slider', // 这个 dataZoom 组件是 inside 型 dataZoom 组件（能在坐标系内进行拖动，以及用滚轮（或移动触屏上的两指滑动）进行缩放）
            xAxisIndex: 0, // 控制x轴
            start: 0,
            end: 100
          }
        ],
        yAxis: {
          type: 'value',
          splitLine: {
            show: true,
            lineStyle: {
              color: ['#dedede']
            }
          },
          axisLine: {
            lineStyle: {
              color: '#d2d2d2',
              width: 1 // 这里是坐标轴的宽度
            }
          }
        },
        series: itemDatas
      }
      // itemChart.setOption(option);
    },

    loadTableData (clear, type) {
      if (clear) {
        this.pageInfo.pageIndex = 1
      }
      const DeviceStatusMap = {
        1: this.$t('common.text.run'),
        0: this.$t('common.text.stop')
      }

      getDeviceList({
        pageNumber: this.pageInfo.pageIndex,
        pageSize: this.pageInfo.pageSize,
        name: this.search.name,
        deviceStatus: this.search.status,
        mode: 'control'
      })
        .then(res => {
          this.tableData = (res.data.result || []).map(d => ({
            ...d,
            deviceStatusName: DeviceStatusMap[d.deviceStatus]
          }))
          console.log(this.tableData)
          if (this.tableData.length > 0) {
            this.deviceId = this.tableData[0].id
            this.controlType = this.tableData[0].deviceControlMode
          }
          if (type) {
            this.clickLi(this.tableData[0], 0)
          }
          this.pageInfo.totalElements = res.data.totalElements || 0
        })
        .finally(() => {
          // this.pageInfo.loading = false;
        })
    },
    onPaginationChange (index) {
      this.pageInfo.pageIndex = index
      this.loadTableData(false)
    },
    onPaginationSizeChange (size) {
      this.pageInfo.pageSize = size
      this.loadTableData(true)
    }
  },
  mounted () {
    this.loadTableData(true, 1)
    // this.loadRealData()
    // this.loadLineData()
  }
}
</script>

<style lang="less" scoped>
@import "../../../../style/varient";

@size-padding: 10px;
/deep/.ivu-input {
  .ivu-input-default {
    width: 200px !important;
  }
}
.ivu-switch-checked {
  background: #000000;
  border-color: #000000;
}
.ivu-switch:after {
  background: #0084ff;
}
.real-time-statistics {
  width: 100%;
  height: 100%;
  .content {
    width: 100%;
    height: 100%;
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.05),
      /*上边阴影  红色*/ -1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*左边阴影  绿色*/ 1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*右边阴影  蓝色*/ 0px 1px 2px 0px rgba(0, 0, 0, 0.05); /*下边阴影  黄色*/
    padding: 24px;
  }
}

.real-time-statistics {
  height: 100%;
  padding: 0 20px 20px 20px;

  .green {
    color: @color-success;
  }

  .red {
    color: @color-warning;
  }

  .container-box {
    width: 100%;
    height: 100%;
    /*background: black;*/
    display: flex;

    .left {
      width: 240px;
      padding-right: 10px;
      height: 100%;
      overflow-y: auto;
      /*background: rgb(16, 86, 244);*/

      .left-title {
        /*position: relative;*/
        position: sticky;
        top: 0;
        // background: white;
        background: rgba(0, 0, 0, 1);
        .label {
          padding: 5px 10px;
        }
        span {
          /*font-weight: bold;*/
          font-size: 14px;
        }

        .icon {
          position: absolute;
          bottom: 8px;
          right: 10px;
          color: rgba(255, 255, 255, 0.3);
          font-size: 12px;
        }

        .icon:hover {
          cursor: pointer;
        }

        .serch-input {
          margin-top: 0;
          background: #262c3a;
        }
      }

      .left-box {
        /*height: 120%;*/
        width: 100%;

        li {
          list-style: none;
          margin: 10px 0;
          // border: 1px solid #d2d2d2;
          border: 1px solid rgba(0, 0, 0, 0.6);

          .li-title {
            height: 35px;
            // color: black;
            color: #eee;
            line-height: 35px;
            // background: #eee;
            background: #000;
            display: flex;
            padding: 0 10px;

            .li-left {
              /*color: black;*/
              flex: 1;
              font-size: 14px;
              overflow: hidden;
              /*font-weight: bold;*/
              text-overflow: ellipsis;
              white-space: nowrap;
            }

            .li-right {
              text-align: right;
              font-size: 12px;
            }
          }

          .li-content {
            padding: 10px;
            font-size: 12px;

            .li-code {
              height: 28px;
              line-height: 28px;
              /*color: #999;*/
              display: flex;

              span:first-child {
                color: #888;
                // width: 60px;
                /*font-weight: bold;*/
              }

              span:last-child {
                color: #999;
                flex: 1;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
              }
            }
          }
        }

        li:hover {
          cursor: pointer;
        }

        .active {
          border-color: rgba(0, 132, 255, 0.5);
          box-shadow: 0px 6px 12px rgba(0, 132, 255, 0.1);
          .li-title {
            background-color: rgba(0, 132, 255, 1);
          }
          .li-left {
            color: rgba(255, 255, 255, 1);
          }
        }
      }
    }

    .right {
      flex: 1;
      height: 100%;
      overflow-y: auto;
      padding-left: 15px;

      .right-title {
        color: #fff;
        margin-bottom: 15px;
      }

      .right-box {
        width: 100%;
        // max-height: 260px;
        // overflow-y: auto;

        .item-list {
          margin-top: 5px;
          display: flex;
          flex-wrap: wrap;
          background: #000;
          border-radius: 5px;

          .item {
            width: calc(~"20% - " 22px);
            margin: 10px;
            position: relative;
            cursor: pointer;

            &-top {
              height: 32px;
              padding-left: 12px;
              border: 1px solid rgba(0, 0, 0, 0.10196078431372549);
              background-color: rgba(33, 34, 39, 0.6);
              // color: rgba(0, 0, 0, 1);
              color: #fff;
              font-size: 14px;
              line-height: 32px;
            }
            &-bottom {
              height: 76px;
              border: 1px solid rgba(0, 0, 0, 0.10196078431372549);
              border-top: none;
              // background-color: #fff;
              background-color: #262c3a;
              line-height: 76px;
              text-align: center;
              span {
                &:first-child {
                  margin-right: 8px;
                  color: #eee;
                  font-size: 18px;
                  font-family: Alibaba PuHuiTi;
                  font-weight: 400;
                }
                &:last-child {
                  color: #888;
                  font-size: 12px;
                  font-family: Alibaba PuHuiTi;
                  font-weight: 400;
                }
              }
            }

            .item:hover {
              cursor: pointer;
            }
          }

          .item-active {
            box-shadow: 0px 6px 12px rgba(0, 132, 255, 0.1);
            .item-top {
              background-color: rgba(0, 132, 255, 1);
              color: rgba(255, 255, 255, 1);
            }
            .itme-top,
            .item-bottom {
              border-color: rgba(0, 132, 255, 0.5);
            }
          }
        }
      }

      .chart-title {
        margin-top: 10px;
        display: flex;
      }

      .chart-box {
        margin-top: 15px;
        width: 100%;
        height: 220px;

        .chartBase {
          width: 100%;
          height: 100%;
        }

        #lineEChart {
          width: 100%;
          height: 100%;
        }
      }
    }

    /*滚动条样式*/
    /*.right:hover{*/
    /*  overflow-y: auto;*/
    /*}*/

    .left::-webkit-scrollbar,
    .right::-webkit-scrollbar,
    .right-box::-webkit-scrollbar {
      /*滚动条整体样式*/
      width: 6px; /*高宽分别对应横竖滚动条的尺寸*/
      height: 8px;
    }

    .left::-webkit-scrollbar-thumb,
    .right::-webkit-scrollbar-thumb,
    .right-box::-webkit-scrollbar-thumb {
      /*滚动条里面小方块*/
      /*border-radius: 5px;*/
      // -webkit-box-shadow: inset 0 0 5px rgba(114, 104, 104, 0.8);
      background: #eee;
    }

    .left::-webkit-scrollbar-track,
    .right::-webkit-scrollbar-track,
    .right-box::-webkit-scrollbar-track {
      /*滚动条里面轨道*/
      // -webkit-box-shadow: inset 0 0 5px rgba(114, 104, 104, 0.8);
      border-radius: 0;
      background: transparent;
    }
  }

  .table-filters {
    display: inline-block;

    .filter-item {
      display: inline-block;
      margin-right: 20px;

      .label {
        font-size: @size-font-normal;
        /*font-weight: normal;*/
        /*font-stretch: normal;*/
        /*letter-spacing: 1px;*/
        color: #1f1f1f;
        padding: 0 9px;
        max-height: 7px;
        display: inline-block;
        min-width: 4em;
      }

      .filter-input {
        width: 170px;
        font-size: @size-font-normal;
      }
    }
  }

  .header {
    height: 60px;
    line-height: 60px;
  }

  .content {
    position: relative;
    padding-bottom: 20px;

    .user-table {
      /*border:1px solid #d2d2d2;*/

      .ivu-table-row.even {
        background-color: #e5f5fe;
      }
    }

    .pagination {
      margin: 20px 0;
      text-align: right;
    }
  }
}
.control-menu {
  display: flex;
  justify-content: space-between;
}
.common-flex {
  display: flex;
  align-items: center;
}
.right-boxes {
  display: flex;
  flex-wrap: wrap;
  margin-top: 20px;
  overflow: scroll;
  max-height: 380px;
  .box {
    text-align: center;
    width: 240px;
    background: #1b1f28;
    margin-right: 24px;
    margin-bottom: 20px;
    // border: 1px solid #aaa;
    height: 107px;
    // border: 1px solid #0084FF;
    &:nth-child(5n) {
      margin-right: 0 !important;
    }
    .nameline {
      padding: 6px 0 6px 12px;
      background: #262c3a;
      border: 1px solid #000;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      word-wrap: normal;
    }
    .value {
      font-size: 18px;
      font-family: Alibaba PuHuiTi;
      line-height: 32px;
      color: #ffffff;
      display: flex;
      align-items: center;
      justify-content: center;
      height: 76px;
    }
  }
}
</style>



