import axios from '@/libs/api.request'

export const getQiTingfenkong = (params) => {
  return axios.request({
    url: '/control/enum/status?deviceId=' + params.deviceId,
    method: 'get'
  })
}

export const getGonglvModefenkong = (params) => {
  return axios.request({
    url: '/control/enum/power/control/mode?deviceId=' + params.deviceId,
    method: 'get'
  })
}

export const getSystemOutput = (params) => {
  return axios.request({
    url: '/control/enum/system/output/power?deviceId=' + params.deviceId,
    method: 'get'
  })
}

export const getIfClear = (params) => {
  return axios.request({
    url: '/control/enum/accident/clear?deviceId=' + params.deviceId,
    method: 'get'
  })
}

export const getSystemStatus = (params) => {
  return axios.request({
    url: '/control/enum/system/status/set?deviceId=' + params.deviceId,
    method: 'get'
  })
}

export const getSystemAutofenpei = (params) => {
  return axios.request({
    url: '/control/enum/system/power/auto/assign?deviceId=' + params.deviceId,
    method: 'get'
  })
}

export const getBingLi = (params) => {
  return axios.request({
    url: '/control/enum/line?deviceId=' + params.deviceId,
    method: 'get'
  })
}

export const getWaiting = (params) => {
  return axios.request({
    url: '/control/enum/standby/mode?deviceId=' + params.deviceId,
    method: 'get'
  })
}

export const getYouGong = (params) => {
  return axios.request({
    url: '/control/enum/active/power/set?deviceId=' + params.deviceId,
    method: 'get'
  })
}

export const getWuGong = (params) => {
  return axios.request({
    url: '/control/enum/reActive/power/set?deviceId=' + params.deviceId,
    method: 'get'
  })
}

export const postOrder = (params) => {
  return axios.request({
    url: '/device/order/delivery',
    data: params,
    method: 'post'
  })
}




